import { forwardRef } from 'react';
import type { PropsWithoutRef, MouseEvent } from 'react';

import { useConsolidatedRef, useI18n } from '../../hooks';
import type { Action, NoChildrenProp, RefElement, WithAttributes } from '../../types';
import Button from '../Button';
import type { ButtonProps } from '../Button';
import Icon from '../Icon';
import MenuButton from '../MenuButton';
import type { MenuButtonProps } from '../MenuButton';

export type ActionsProps = WithAttributes<
  'button',
  NoChildrenProp & {
    items: Action[];
    contextualLabel?: string;
    /** @default 2 */
    menuAt?: number;
    menuButtonProps?: {
      onClick?: MenuButtonProps['onClick'];
      onKeyDown?: MenuButtonProps['onKeyDown'];
    };
    /** @default true */
    iconOnly?: boolean;
    /** @default false */
    compact?: ButtonProps['compact'];
  }
>;

export default forwardRef<RefElement<ActionsProps>, PropsWithoutRef<ActionsProps>>(function Actions(
  { items, menuAt = 2, iconOnly = true, menuButtonProps, contextualLabel, compact = false },
  ref
) {
  const t = useI18n();
  const menuButtonRef = useConsolidatedRef(ref);

  if (!items || items.length === 0) {
    return null;
  }

  return items.length >= menuAt ? (
    <MenuButton
      ref={menuButtonRef}
      text={t('actions')}
      aria-label={contextualLabel && t('actions_for', [contextualLabel])}
      iconOnly
      icon='more'
      variant='simple'
      compact={compact}
      onClick={menuButtonProps?.onClick}
      onKeyDown={menuButtonProps?.onKeyDown}
      menu={{
        items: items.map(({ text, onClick, ...restProps }) => ({
          ...restProps,
          primary: text,
          onClick: onClick
            ? (id, e) => {
                onClick(id, e, menuButtonRef.current ?? undefined);
              }
            : undefined
        }))
      }}
    />
  ) : (
    <>
      {items.map(({ id, icon, text, onClick, ...restProps }) => {
        return (
          <Button
            key={id}
            variant={icon && iconOnly ? 'simple' : undefined}
            onClick={(event: MouseEvent<HTMLButtonElement>) => onClick?.(id, event)}
            label={icon ? text : undefined}
            icon={!!icon && iconOnly}
            compact={compact}
            aria-label={contextualLabel ? `${text} - ${contextualLabel}` : text}
            {...restProps}
          >
            {icon && iconOnly ? <Icon name={icon} /> : text}
          </Button>
        );
      })}
    </>
  );
});
