import { cloneElement, useEffect, forwardRef, useState, useRef } from 'react';
import type { FC, ReactNode, ComponentType, ReactElement, PropsWithoutRef, Ref } from 'react';
import styled, { css } from 'styled-components';
import { readableColor, rgba } from 'polished';

import { defaultThemeProp } from '../../theme';
import type { Action, ExcludeStrict, ForwardProps, OmitStrict, TestIdProp } from '../../types';
import Flex from '../Flex';
import type { FlexProps } from '../Flex';
import Actions from '../Actions';
import type { FormControlProps } from '../FormControl';
import {
  useConsolidatedRef,
  useDirection,
  useI18n,
  useLiveLog,
  useTestIds,
  useUID
} from '../../hooks';
import Label, { StyledLabel } from '../Label';
import type { LabelProps } from '../Label';
import { calculateFontSize, getHoverColors } from '../../styles';
import VisuallyHiddenText from '../VisuallyHiddenText';
import { getFocusables, tryCatch, withTestIds } from '../../utils';
import Button from '../Button';
import Icon, { registerIcon } from '../Icon';
import * as warnSolidIcon from '../Icon/icons/warn-solid.icon';
import * as flagWaveSolidIcon from '../Icon/icons/flag-wave-solid.icon';
import * as checkIcon from '../Icon/icons/check.icon';
import AdditionalInfo, { StyledAdditionalInfo } from '../AdditionalInfo';

import { getFormFieldTestIds } from './FormField.test-ids';
import type { elements } from './FormField.test-ids';

registerIcon(warnSolidIcon, flagWaveSolidIcon, checkIcon);

export interface FormFieldProps
  extends OmitStrict<FormControlProps, 'placeholder' | 'value' | 'defaultValue'>,
    TestIdProp<typeof elements> {
  /** Pass a single form control component i.e. Input | TextArea | Select | etc.. */
  children: ReactElement | ReactElement[];
  /**
   * An id is required to connect a FormField's wrapping element, control element and info(aria-describedby) live region.
   * A random id will be generated if none is provided.
   * Since MOST FormControls should possess an id(excl. i.e RadioCheckGroup and others) this can be used.
   * The id prop will be used to generate the following:
   * - FormControl <-> Label association via a Label's htmlFor prop.
   * - FormField's wrapping element id {id}-field.
   * - FormField info element id ${id}-info.
   */
  id?: string;
  /**
   * Determines how the wrapping label should be rendered.
   * @default 'label'
   */
  labelAs?: LabelProps['as'];
  /**
   * Overrides the for attribute on the label. If this is not set it will default to the forms id.
   * @default undefined
   */
  labelFor?: LabelProps['htmlFor'];
  /**
   * Sets the id of the label.
   * @default undefined
   */
  labelId?: LabelProps['id'];
  /**
   * Visually hides the label region.
   * @default false
   */
  labelHidden?: boolean;
  /**
   * Visually places the label after the input.
   * @default false
   */
  labelAfter?: boolean;
  /**
   * Property used to check whether input type is radio to handle styling for RadioCheck
   * @default false
   */
  isRadioCheck?: boolean;
  /**
   * Wrapping HTML element tag. Renders as a fieldset for grouped elements i.e. RadioButtons/CheckboxGroup
   * @default 'div'
   */
  as?: 'div' | 'fieldset' | ComponentType<any>;
  /**
   * Layout field elements inline in a row.
   * @default false
   */
  inline?: boolean;
  /** Optionally utilized by Input or Select. */
  actions?: Action[];
  /** Enables Flex container prop pass through. */
  container?: ExcludeStrict<FlexProps['container'], true>;
  /** Character remaining count. Typically used on Textareas only */
  charLimitDisplay?: ReactNode;
}

const StyledStatusIcon = styled(Icon)<{ status: NonNullable<FormFieldProps['status']> }>(
  ({ theme, status }) => {
    return css`
      height: 1em;
      width: 1em;
      color: ${theme.components['form-field'][status]['status-color']};
      vertical-align: baseline;
      margin-inline-end: 0.5ch;
    `;
  }
);

StyledStatusIcon.defaultProps = defaultThemeProp;

export const StyledFormFieldInfo = styled.div<Pick<FormControlProps, 'status'>>(
  ({
    status,
    theme: {
      base: { 'font-size': fontSize, 'font-scale': fontScale, spacing },
      components: { 'form-field': formField }
    }
  }) => {
    const { xxs: infoFontSize } = calculateFontSize(fontSize, fontScale);

    return css`
      max-width: max-content;
      margin-top: calc(0.25 * ${spacing});
      font-size: ${infoFontSize};
      word-break: break-word;
      ${status &&
      formField[status] &&
      css`
        color: ${formField[status]['status-color']};
      `}
    `;
  }
);

StyledFormFieldInfo.defaultProps = defaultThemeProp;

export const StyledRequiredAsterisk = styled.span(({ theme }) => {
  const {
    base: {
      palette: { urgent }
    }
  } = theme;

  return css`
    vertical-align: top;
    color: ${urgent};
  `;
});

StyledRequiredAsterisk.defaultProps = defaultThemeProp;

const StyledLabelRow = styled.div`
  ${StyledRequiredAsterisk} {
    margin-inline-end: auto;
  }
`;

export const StyledFormField = styled.div<
  FormFieldProps & { labelAsLegend: boolean; showAdditionalInfo: boolean }
>(props => {
  const {
    labelAsLegend,
    showAdditionalInfo,
    disabled,
    required,
    theme: {
      base: { 'disabled-opacity': disabledOpacity, spacing }
    }
  } = props;
  const { ltr } = useDirection();

  return css`
    ${disabled &&
    css`
      opacity: ${disabledOpacity};
      -webkit-user-select: none;
      user-select: none;
    `}
    position: relative;
    border: 0;

    > ${StyledLabel}, > ${StyledLabelRow} {
      margin-bottom: calc(0.25 * ${spacing});
    }

    > ${StyledLabel}, > ${StyledLabelRow} > ${StyledLabel} {
      ${disabled &&
      css`
        cursor: not-allowed;
      `}
    }

    ${labelAsLegend &&
    (showAdditionalInfo || required) &&
    css`
      display: block;

      > legend {
        float: ${ltr ? 'left' : 'right'};
      }

      > ${StyledRequiredAsterisk} {
        float: ${ltr ? 'left' : 'right'};

        ${!showAdditionalInfo &&
        css`
          + * {
            clear: both;
          }
        `}
      }

      > ${StyledAdditionalInfo} {
        float: ${ltr ? 'right' : 'left'};

        + * {
          clear: both;
        }
      }
    `}
  `;
});

StyledFormField.defaultProps = defaultThemeProp;

const StyledSuggestionButton = styled(Button)(
  ({
    theme: {
      base: {
        'font-size': fontSize,
        'font-scale': fontScale,
        'border-radius': baseRadius,
        spacing
      },
      components: {
        'form-control': { 'border-radius': radius, 'border-width': borderWidth },
        'form-field': formField
      }
    }
  }) => {
    const { xxs: buttonFontSize } = calculateFontSize(fontSize, fontScale);
    const backgroundColor = formField.pending['status-color'];
    const hoverColors = getHoverColors(backgroundColor);
    const color = tryCatch(() => readableColor(backgroundColor));
    const borderColor = color ? rgba(color, 0.4) : color;

    return css`
      background-color: ${backgroundColor};
      color: ${color};
      font-size: ${buttonFontSize};
      min-width: calc(3 * ${spacing});
      min-height: calc(3 * ${spacing});
      padding: 0 ${spacing};
      border-radius: 0;
      border: none;
      &:first-child {
        border-inline-end: ${borderWidth} solid ${borderColor};
        border-end-start-radius: calc(${baseRadius} * ${radius});
        margin-inline-start: calc(2 * ${spacing});
      }
      &:last-child {
        border-end-end-radius: calc(${baseRadius} * ${radius});
        margin-inline-start: 0;
      }
      &:hover {
        color: ${hoverColors.foreground};
        background-color: ${hoverColors.background};
      }

      @media (pointer: coarse) {
        min-height: 2rem;
      }
    `;
  }
);

StyledSuggestionButton.defaultProps = defaultThemeProp;

const statusIconMap = { error: 'warn-solid', warning: 'flag-wave-solid', success: 'check' };

const FormField: FC<FormFieldProps & ForwardProps> = forwardRef(function FormField(
  props: PropsWithoutRef<FormFieldProps>,
  ref: Ref<HTMLElement>
) {
  const uid = useUID();
  const {
    testId,
    children: controlElement,
    id = uid,
    as = 'div',
    label,
    labelAs = 'label',
    labelFor = id,
    labelId,
    labelHidden = false,
    labelAfter = false,
    info,
    status,
    isRadioCheck,
    charLimitDisplay,
    required = false,
    disabled = false,
    readOnly = false,
    inline = false,
    actions,
    container,
    additionalInfo,
    onResolveSuggestion,
    ...restProps
  } = props;

  const testIds = useTestIds(testId, getFormFieldTestIds);

  const t = useI18n();
  const { announceAssertive } = useLiveLog();
  const labelAsLegend: boolean = labelAs === 'legend';
  const hasSuggestion = status === 'pending' && !!onResolveSuggestion;
  const consolidatedRef = useConsolidatedRef(ref);
  const labelRef = useRef<HTMLLabelElement | HTMLLegendElement>(null);
  const [labelText, setLabelText] = useState<string | null>(null);

  const showAdditionalInfo = !!additionalInfo && !disabled && !labelHidden;

  const labelContent = (
    <>
      <Label
        data-testid={testIds.label}
        id={labelId}
        as={labelAs}
        htmlFor={labelAs === 'label' ? labelFor : undefined}
        labelHidden={labelHidden}
        onClick={(e: MouseEvent) => {
          if (readOnly) e.preventDefault();
        }}
        inline={inline}
        ref={labelRef}
      >
        {label}
      </Label>

      {required && !labelHidden && (
        <StyledRequiredAsterisk aria-hidden>&nbsp;*</StyledRequiredAsterisk>
      )}

      {showAdditionalInfo && labelText && (
        <AdditionalInfo
          data-testid={testIds.additionalInfo}
          heading={additionalInfo.heading}
          contextualLabel={labelText}
        >
          {additionalInfo.content}
        </AdditionalInfo>
      )}
    </>
  );

  const labelRow =
    !isRadioCheck && !labelAsLegend ? (
      <Flex
        as={StyledLabelRow}
        container={{ justify: 'between', alignItems: 'end' }}
        item={{ alignSelf: 'stretch' }}
      >
        {labelContent}
      </Flex>
    ) : (
      labelContent
    );

  useEffect(() => {
    setLabelText(labelRef.current?.textContent ?? null);
  }, [label]);

  useEffect(() => {
    if (status === 'error' || status === 'warning') {
      let message = `${t(status)} ${info}`;
      if (labelRef.current?.textContent) message = `${labelRef.current?.textContent} ${message}`;
      announceAssertive({ message, type: status });
    }
  }, [status, info]);

  // fieldset or single form control element
  let content: FormFieldProps['children'] = labelAsLegend
    ? controlElement
    : cloneElement(controlElement as ReactElement, {
        'aria-describedby': info && `${id}-info`
      });

  if (actions) {
    content = (
      <Flex container={{ alignItems: 'center', gap: 0.5 }}>
        {content}
        <Actions items={actions} menuAt={3} />
      </Flex>
    );
  }

  let infoContent = (
    <Flex
      id={`${id}-info`}
      data-testid={testIds.info}
      status={status}
      as={StyledFormFieldInfo}
      container={{ alignItems: 'center' }}
    >
      {status && status !== 'pending' && (
        <>
          <StyledStatusIcon status={status} name={statusIconMap[status]} />
          <VisuallyHiddenText>{`${t(status)} `}</VisuallyHiddenText>
        </>
      )}
      {info}
    </Flex>
  );

  if (charLimitDisplay) {
    infoContent = (
      <Flex container={{ justify: infoContent ? 'between' : 'end', gap: 1 }}>
        {infoContent}
        <Flex item={{ shrink: 0 }}>{charLimitDisplay}</Flex>
      </Flex>
    );
  }

  if (hasSuggestion) {
    const focusables = getFocusables(consolidatedRef);
    infoContent = (
      <Flex container={{ alignItems: 'start', justify: 'between' }}>
        <StyledFormFieldInfo data-testid={testIds.info} status={status} id={`${id}-info`}>
          {t('suggestion_info')}
          <VisuallyHiddenText aria-live='polite'>{t('suggestion_assist')}</VisuallyHiddenText>
        </StyledFormFieldInfo>
        <Flex container={{ wrap: 'nowrap' }}>
          <StyledSuggestionButton
            data-testid={testIds.suggestionReject}
            onClick={() => {
              onResolveSuggestion(false);
              focusables[0]?.focus();
            }}
            aria-label={t('reject_suggestion_button_a11y')}
          >
            {t('no')}
          </StyledSuggestionButton>
          <StyledSuggestionButton
            data-testid={testIds.suggestionAccept}
            onClick={() => {
              onResolveSuggestion(true);
              focusables[0]?.focus();
            }}
            aria-label={t('accept_suggestion_button_a11y')}
          >
            {t('yes')}
          </StyledSuggestionButton>
        </Flex>
      </Flex>
    );
  }

  const onKeyDown = (e: KeyboardEvent) => {
    if (e.key === 'Enter') {
      const target = e.target as HTMLElement;
      if (!target.closest('button')) {
        e.preventDefault();
        onResolveSuggestion?.(true);
      }
    }
  };

  return (
    <Flex
      data-testid={testIds.root}
      {...restProps}
      container={{
        direction: inline ? 'row' : 'column',
        alignItems: inline ? 'center' : undefined,
        ...container
      }}
      as={StyledFormField}
      labelAsLegend={labelAsLegend}
      showAdditionalInfo={showAdditionalInfo}
      id={`${id}-field`}
      forwardedAs={as}
      required={required}
      disabled={disabled}
      readOnly={readOnly}
      onKeyDown={hasSuggestion ? onKeyDown : undefined}
      aria-describedby={labelAsLegend && info ? `${id}-info` : undefined}
      ref={consolidatedRef}
    >
      {(labelAsLegend || !labelAfter) && labelRow}
      {content}
      {!labelAsLegend && labelAfter && labelRow}
      {!readOnly && infoContent}
    </Flex>
  );
});

export default withTestIds(FormField, getFormFieldTestIds);
